body {
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 50px;
}

.btn {
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
}

.btn:disabled {
  background-color: black;
  cursor: not-allowed;
}

.animate {
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
